                        div 
                            p
                                | Nutzer knnen ein Nutzerkonto anlegen. Im Rahmen der Registrierung werden den Nutzern die erforderlichen
                                | Pflichtangaben mitgeteilt und zu Zwecken der Bereitstellung des Nutzerkontos auf Grundlage vertraglicher
                                | Pflichterfllung verarbeitet. Zu den verarbeiteten Daten gehren insbesondere die Login-Informationen (Name,
                                | Passwort sowie eine E-Mail-Adresse). Die im Rahmen der Registrierung eingegebenen Daten werden fr die Zwecke der
                                | Nutzung des Nutzerkontos und dessen Zwecks verwendet.
                            p
                                | Die Nutzer knnen ber Vorgnge, die fr deren Nutzerkonto relevant sind, wie z.B. technische nderungen, per E-Mail
                                | informiert werden. Wenn Nutzer ihr Nutzerkonto gekndigt haben, werden deren Daten im Hinblick auf das Nutzerkonto,
                                | vorbehaltlich einer gesetzlichen Aufbewahrungspflicht, gelscht. Es obliegt den Nutzern, ihre Daten bei erfolgter
                                | Kndigung vor dem Vertragsende zu sichern. Wir sind berechtigt, smtliche whrend der Vertragsdauer gespeicherte
                                | Daten des Nutzers unwiederbringlich zu lschen.
                            p
                                | Im Rahmen der Inanspruchnahme unserer Registrierungs- und Anmeldefunktionen sowie der Nutzung des Nutzerkontos
                                | speichern wir die IP-Adresse und den Zeitpunkt der jeweiligen Nutzerhandlung. Die Speicherung erfolgt auf Grundlage
                                | unserer berechtigten Interessen als auch jener der Nutzer an einem Schutz vor Missbrauch und sonstiger unbefugter
                                | Nutzung. Eine Weitergabe dieser Daten an Dritte erfolgt grundstzlich nicht, es sei denn, sie ist zur Verfolgung
                                | unserer Ansprche erforderlich oder es besteht hierzu besteht eine gesetzliche Verpflichtung.
                            p
                                strong Online-Forum
                                | : Die Teilnahme am Forum setzt eine Registrierung voraus, bei der, vorbehaltlich anderer
                                |     Angaben im Registrierungsformular, ein oder Ihr Name, ein Kennwort und die E-Mail-Adresse, an welche die
                                |     Zugangsdaten versendet werden, angegeben werden mssen. Aus Grnden der Sicherheit sollte das Kennwort dem Stand der
                                |     Technik entsprechen, also kompliziert sein (hierauf werden Nutzer notfalls im Rahmen der Registrierung hingewiesen)
                                |     und nicht an anderer Stelle verwendet werden. Die Beitrge im Forum sind fr die ffentlichkeit sichtbar, es sei
                                |     denn, deren Sichtbarkeit ist auf bestimmte Mitglieder oder Mitgliedergruppen beschrnkt. Die Beitrge der Verfasser
                                |     werden mit deren Namen, sofern registriert oder angegeben, dem Zeitpunkt und dem Eintragsinhalt gespeichert. Bei
                                |     Anmeldungen und beim Verfassen von Eintrgen werden ferner die IP-Adressen der Nutzer gespeichert, falls die
                                |     Eintrge einen unzulssigen Inhalt aufweisen sollten und die IP-Adressen der Rechtsverfolgung dienen knnten. Der
                                |     Verantwortliche behlt sich vor, die Anmeldungen und Eintrge auf Grundlage einer sachgerechten Abwgung zu lschen.
                            p
                                strong Zwei-Faktor-Authentifizierung
                                | : Die Zwei-Faktor-Authentifizierung bietet eine zustzliche
                                |     Sicherheitsebene fr Ihr Benutzerkonto und stellt sicher, dass nur Sie auf Ihr Konto zugreifen knnen, auch, wenn
                                |     jemand anderes Ihr Passwort kennt.
                            p
                                | Zu diesem Zweck mssen Sie zustzlich zu Ihrem Passwort eine weitere Authentifizierungsmanahme durchfhren (z.B.
                                | einen an ein mobiles Gert gesandten Code eingeben). Wir werden Sie ber das von uns eingesetzte Verfahren
                                | informieren.
                            ul.m-elements
