                    .text-container
                        div 
                            p
                                | Wir nutzen Blogs oder vergleichbare Mittel der Onlinekommunikation und Publikation (nachfolgend
                                | "Publikationsmedium"). Die Daten der Leser werden fr die Zwecke des Publikationsmediums nur insoweit verarbeitet,
                                | als es fr dessen Darstellung und die Kommunikation zwischen Autoren und Lesern oder aus Grnden der Sicherheit
                                | erforderlich ist. Im brigen verweisen wir auf die Informationen zur Verarbeitung der Besucher unseres
                                | Publikationsmediums im Rahmen dieser Datenschutzhinweise.
                            p
                                strong Kommentare und Beitrge
                                | : Wenn Nutzer Kommentare oder sonstige Beitrge hinterlassen, knnen ihre
                                |     IP-Adressen auf Grundlage unserer berechtigten Interessen gespeichert werden. Das erfolgt zu unserer Sicherheit,
                                |     falls jemand in Kommentaren und Beitrgen widerrechtliche Inhalte hinterlsst (Beleidigungen, verbotene politische
                                |     Propaganda etc.). In diesem Fall knnen wir selbst fr den Kommentar oder Beitrag belangt werden und sind daher an
                                |     der Identitt des Verfassers interessiert.
                            p
                                | Des Weiteren behalten wir uns vor, auf Grundlage unserer berechtigten Interessen die Angaben der Nutzer zwecks
                                | Spamerkennung zu verarbeiten.
                            p
                                | Auf derselben Rechtsgrundlage behalten wir uns vor, im Fall von Umfragen die IP-Adressen der Nutzer fr deren Dauer
                                | zu speichern und Cookies zu verwenden, um Mehrfachabstimmungen zu vermeiden.
                            p
                                | Die im Rahmen der Kommentare und Beitrge mitgeteilten Informationen zur Person, etwaige Kontakt- sowie
                                | Webseiteninformationen als auch die inhaltlichen Angaben werden von uns bis zum Widerspruch der Nutzer dauerhaft
                                | gespeichert.
                            p
                                strong Kommentarabonnements
                                | : Die Nachfolgekommentare knnen von Nutzern mit deren Einwilligung abonniert
                                |     werden. Die Nutzer erhalten eine Besttigungsemail, um zu berprfen, ob sie der Inhaber der eingegebenen
                                |     Emailadresse sind. Nutzer knnen laufende Kommentarabonnements jederzeit abbestellen. Die Besttigungsemail wird
                                |     Hinweise zu den Widerrufsmglichkeiten enthalten. Fr die Zwecke des Nachweises der Einwilligung der Nutzer
                                |     speichern wir den Anmeldezeitpunkt nebst der IP-Adresse der Nutzer und lschen diese Informationen, wenn Nutzer sich
                                |     von dem Abonnement abmelden.
                            p
                                | Sie knnen den Empfang unseres Abonnements jederzeit kndigen, d.h., Ihre Einwilligungen widerrufen. Wir knnen die
                                | ausgetragenen E-Mail-Adressen bis zu drei Jahren auf Grundlage unserer berechtigten Interessen speichern, bevor wir
                                | sie lschen, um eine ehemals gegebene Einwilligung nachweisen zu knnen. Die Verarbeitung dieser Daten wird auf den
                                | Zweck einer mglichen Abwehr von Ansprchen beschrnkt. Ein individueller Lschungsantrag ist jederzeit mglich,
                                | sofern zugleich das ehemalige Bestehen einer Einwilligung besttigt wird.
                            p
                                strong Profilbilder von Gravatar
                                | : Wir setzen innerhalb unseres Onlineangebotes und insbesondere im Blog den
                                |     Dienst Gravatar ein.
                            p
                                | Gravatar ist ein Dienst, bei dem sich Nutzer anmelden und Profilbilder und ihre E-Mail-Adressen hinterlegen knnen.
                                | Wenn Nutzer mit der jeweiligen E-Mail-Adresse auf anderen Onlineprsenzen (vor allem in Blogs) Beitrge oder
                                | Kommentare hinterlassen, knnen deren Profilbilder neben den Beitrgen oder Kommentaren dargestellt werden. Hierzu
                                | wird die von den Nutzern mitgeteilte E-Mail-Adresse an Gravatar zwecks Prfung, ob zu ihr ein Profil gespeichert
                                | ist, verschlsselt bermittelt. Dies ist der einzige Zweck der bermittlung der E-Mail-Adresse. Sie wird nicht fr
                                | andere Zwecke verwendet, sondern danach gelscht.
                            p
                                | Die Nutzung von Gravatar erfolgt auf Grundlage unserer berechtigten Interessen, da wir mit Hilfe von Gravatar den
                                | Beitrags- und Kommentarverfassern die Mglichkeit bieten, ihre Beitrge mit einem Profilbild zu personalisieren.
                            p
                                | Durch die Anzeige der Bilder bringt Gravatar die IP-Adresse der Nutzer in Erfahrung, da dies fr eine Kommunikation
                                | zwischen einem Browser und einem Onlineservice notwendig ist.
                            p
                                | Wenn Nutzer nicht mchten, dass ein mit ihrer E-Mail-Adresse bei Gravatar verknpftes Benutzerbild in den Kommentaren
                                | erscheint, sollten sie zum Kommentieren eine E-Mail-Adresse nutzen, welche nicht bei Gravatar hinterlegt ist. Wir
                                | weisen ferner darauf hin, dass es auch mglich ist eine anonyme oder gar keine E-Mail-Adresse zu verwenden, falls
                                | die Nutzer nicht wnschen, dass die eigene E-Mail-Adresse an Gravatar bersendet wird. Nutzer knnen die bertragung
                                | der Daten komplett verhindern, indem sie unser Kommentarsystem nicht nutzen.
                            ul.m-elements
                                li
                                    p
                                        strong Verarbeitete Datenarten:
                                        |  Bestandsdaten (z.B. Namen, Adressen), Kontaktdaten (z.B. E-Mail,
                                        |             Telefonnummern), Inhaltsdaten (z.B. Texteingaben, Fotografien, Videos), Nutzungsdaten (z.B. besuchte
                                        |             Webseiten, Interesse an Inhalten, Zugriffszeiten), Meta-/Kommunikationsdaten (z.B. Gerte-Informationen,
