                Section#s12(icon="mdi-server" title="Bereitstellung des Onlineangebotes und Webhosting")
                    .text-container
                        div 
                            p
                                | Um unser Onlineangebot sicher und effizient bereitstellen zu knnen, nehmen wir die Leistungen von einem oder
                                | mehreren Webhosting-Anbietern in Anspruch, von deren Servern (bzw. von ihnen verwalteten Servern) das Onlineangebot
                                | abgerufen werden kann. Zu diesen Zwecken knnen wir Infrastruktur- und Plattformdienstleistungen, Rechenkapazitt,
                                | Speicherplatz und Datenbankdienste sowie Sicherheitsleistungen und technische Wartungsleistungen in Anspruch nehmen.
                            p
                                | Zu den im Rahmen der Bereitstellung des Hostingangebotes verarbeiteten Daten knnen alle die Nutzer unseres
                                | Onlineangebotes betreffenden Angaben gehren, die im Rahmen der Nutzung und der Kommunikation anfallen. Hierzu
                                | gehren regelmig die IP-Adresse, die notwendig ist, um die Inhalte von Onlineangeboten an Browser ausliefern zu
                                | knnen, und alle innerhalb unseres Onlineangebotes oder von Webseiten gettigten Eingaben.
                            p
                                strong E-Mail-Versand und -Hosting
                                | : Die von uns in Anspruch genommenen Webhosting-Leistungen umfassen
                                |     ebenfalls den Versand, den Empfang sowie die Speicherung von E-Mails. Zu diesen Zwecken werden die Adressen der
                                |     Empfnger sowie Absender als auch weitere Informationen betreffend den E-Mailversand (z.B. die beteiligten Provider)
                                |     sowie die Inhalte der jeweiligen E-Mails verarbeitet. Die vorgenannten Daten knnen ferner zu Zwecken der Erkennung
                                |     von SPAM verarbeitet werden. Wir bitten darum, zu beachten, dass E-Mails im Internet grundstzlich nicht
                                |     verschlsselt versendet werden. Im Regelfall werden E-Mails zwar auf dem Transportweg verschlsselt, aber (sofern
                                |     kein sogenanntes Ende-zu-Ende-Verschlsselungsverfahren eingesetzt wird) nicht auf den Servern, von denen sie
                                |     abgesendet und empfangen werden. Wir knnen daher fr den bertragungsweg der E-Mails zwischen dem Absender und dem
                                |     Empfang auf unserem Server keine Verantwortung bernehmen.
                            p
                                strong Erhebung von Zugriffsdaten und Logfiles
                                | : Wir selbst (bzw. unser Webhostinganbieter) erheben Daten zu
                                |     jedem Zugriff auf den Server (sogenannte Serverlogfiles). Zu den Serverlogfiles knnen die Adresse und Name der
                                |     abgerufenen Webseiten und Dateien, Datum und Uhrzeit des Abrufs, bertragene Datenmengen, Meldung ber erfolgreichen
                                |     Abruf, Browsertyp nebst Version, das Betriebssystem des Nutzers, Referrer URL (die zuvor besuchte Seite) und im
                                |     Regelfall IP-Adressen und der anfragende Provider gehren.
                            p
                                | Die Serverlogfiles knnen zum einen zu Zwecken der Sicherheit eingesetzt werden, z.B., um eine berlastung der Server
                                | zu vermeiden (insbesondere im Fall von missbruchlichen Angriffen, sogenannten DDoS-Attacken) und zum anderen, um
                                | die Auslastung der Server und ihre Stabilitt sicherzustellen.
                            p
                                strong Content-Delivery-Network
                                | : Wir setzen ein "Content-Delivery-Network" (CDN) ein. Ein CDN ist ein
                                |     Dienst, mit dessen Hilfe Inhalte eines Onlineangebotes, insbesondere groe Mediendateien, wie Grafiken oder
                                |     Programm-Skripte, mit Hilfe regional verteilter und ber das Internet verbundener Server schneller und sicherer
                                |     ausgeliefert werden knnen.
                            ul.m-elements
                                li
                                    p
                                        strong Verarbeitete Datenarten:
                                        |  Inhaltsdaten (z.B. Texteingaben, Fotografien, Videos),
                                        |             Nutzungsdaten (z.B. besuchte Webseiten, Interesse an Inhalten, Zugriffszeiten), Meta-/Kommunikationsdaten
                                        |             (z.B. Gerte-Informationen, IP-Adressen).
                                li
