                                        |  Nutzer (z.B. Webseitenbesucher, Nutzer von Onlinediensten).
                                li
                                    p
                                        strong Zwecke der Verarbeitung:
                                        |  Content Delivery Network (CDN).
                                li
                                    p
                                        strong Rechtsgrundlagen:
                                        |  Berechtigte Interessen (Art. 6 Abs. 1 S. 1 lit. f. DSGVO).

