                    .text-container
                        div 
                            p
                                | Wir nutzen ber das Internet zugngliche und auf den Servern ihrer Anbieter ausgefhrte Softwaredienste (sogenannte
                                | "Cloud-Dienste", auch bezeichnet als "Software as a Service") fr die folgenden Zwecke: Dokumentenspeicherung und
                                | Verwaltung, Kalenderverwaltung, E-Mail-Versand, Tabellenkalkulationen und Prsentationen, Austausch von Dokumenten,
                                | Inhalten und Informationen mit bestimmten Empfngern oder Verffentlichung von Webseiten, Formularen oder sonstigen
                                | Inhalten und Informationen sowie Chats und Teilnahme an Audio- und Videokonferenzen.
                            p
                                | In diesem Rahmen knnen personenbezogenen Daten verarbeitet und auf den Servern der Anbieter gespeichert werden,
                                | soweit diese Bestandteil von Kommunikationsvorgngen mit uns sind oder von uns sonst, wie im Rahmen dieser
                                | Datenschutzerklrung dargelegt, verarbeitet werden. Zu diesen Daten knnen insbesondere Stammdaten und Kontaktdaten
                                | der Nutzer, Daten zu Vorgngen, Vertrgen, sonstigen Prozessen und deren Inhalte gehren. Die Anbieter der
                                | Cloud-Dienste verarbeiten ferner Nutzungsdaten und Metadaten, die von ihnen zu Sicherheitszwecken und zur
                                | Serviceoptimierung verwendet werden.
                            p
                                | Sofern wir mit Hilfe der Cloud-Dienste fr andere Nutzer oder ffentlich zugngliche Webseiten Formulare o.a.
                                | Dokumente und Inhalte bereitstellen, knnen die Anbieter Cookies auf den Gerten der Nutzer fr Zwecke der
                                | Webanalyse oder, um sich Einstellungen der Nutzer (z.B. im Fall der Mediensteuerung) zu merken, speichern.
                            p
                                strong Hinweise zu Rechtsgrundlagen:
                                |  Sofern wir um eine Einwilligung in den Einsatz der Cloud-Dienste
                                |     bitten, ist die Rechtsgrundlage der Verarbeitung die Einwilligung. Ferner kann deren Einsatz ein Bestandteil unserer
                                |     (vor)vertraglichen Leistungen sein, sofern der Einsatz der Cloud-Dienste in diesem Rahmen vereinbart wurde.
                                |     Ansonsten werden die Daten der Nutzer auf Grundlage unserer berechtigten Interessen (d.h., Interesse an effizienten
                                |     und sicheren Verwaltungs- und Kollaborationsprozessen) verarbeitet
                            ul.m-elements
