        v-layout
            v-flex(xs12)
                Section#s14(icon="mdi-chart-areaspline" title="Webanalyse und Optimierung")
                    .text-container
                        div 
                            p
                                | Die Webanalyse (auch als "Reichweitenmessung" bezeichnet) dient der Auswertung der Besucherstrme unseres
                                | Onlineangebotes und kann Verhalten, Interessen oder demographische Informationen zu den Besuchern, wie z.B. das
                                | Alter oder das Geschlecht, als pseudonyme Werte umfassen. Mit Hilfe der Reichweitenanalyse knnen wir z.B. erkennen,
                                | zu welcher Zeit unser Onlineangebot oder dessen Funktionen oder Inhalte am hufigsten genutzt werden oder zur
                                | Wiederverwendung einladen. Ebenso knnen wir nachvollziehen, welche Bereiche der Optimierung bedrfen.
                            p
