nverarbeitung (Bundesdatenschutzgesetz \n    BDSG). Das BDSG enthlt insbesondere Spezialregelungen zum Recht auf Auskunft, zum Recht auf Lschung, zum\n    Widerspruchsrecht, zur Verarbeitung besonderer Kategorien personenbezogener Daten, zur Verarbeitung fr andere\n    Zwecke und zur bermittlung sowie automatisierten Entscheidungsfindung im Einzelfall einschlielich Profiling. Des\n    Weiteren regelt es die Datenverarbeitung fr Zwecke des Beschftigungsverhltnisses ( 26 BDSG), insbesondere im\n    Hinblick auf die Begrndung, Durchfhrung oder Beendigung von Beschftigungsverhltnissen sowie die Einwilligung von\n    Beschftigten. Ferner knnen Landesdatenschutzgesetze der einzelnen Bundeslnder zur Anwendung gelangen.")])])])])],1)],1),_c('v-layout',[_c('v-flex',{attrs:{"xs12":""}},[_c('Section',{attrs:{"id":"s5","icon":"security","title":"Sicherheitsmanahmen"}},[_c('div',{staticClass:"text-container"},[_c('div',[_c('p',[_vm._v("Wir treffen nach Magabe der gesetzlichen Vorgaben unter Bercksichtigung des Stands der Technik, der\nImplementierungskosten und der Art, des Umfangs, der Umstnde und der Zwecke der Verarbeitung sowie der\nunterschiedlichen Eintrittswahrscheinlichkeiten und des Ausmaes der Bedrohung der Rechte und Freiheiten natrlicher\nPersonen geeignete technische und organisatorische Manahmen, um ein dem Risiko angemessenes Schutzniveau zu\ngewhrleisten.")]),_c('p',[_vm._v("Zu den Manahmen gehren insbesondere die Sicherung der Vertraulichkeit, Integritt und Verfgbarkeit von Daten durch\nKontrolle des physischen und elektronischen Zugangs zu den Daten als auch des sie betreffenden Zugriffs, der\nEingabe, der Weitergabe, der Sicherung der Verfgbarkeit und ihrer Trennung. Des Weiteren haben wir Verfahren\neingerichtet, die eine Wahrnehmung von Betroffenenrechten, die Lschung von Daten und Reaktionen auf die Gefhrdung\nder Daten gewhrleisten. Ferner bercksichtigen wir den Schutz personenbezogener Daten bereits bei der Entwicklung\nbzw. Auswahl von Hardware, Software